<template>
   <span>再次获取（{{ myTime }}）</span>
</template>

<script>
 import pubsub from 'pubsub-js'
export default {
  data() {
    return {
      myTime: this.time,
    };
  },
  props: {
    time: {
      type: Number,
      default: 60,
    },
  },
  mounted() {
    this.timer = setInterval(() => {
        if(this.myTime>0){
            this.myTime--
        }
        else{
            pubsub.publish("timeout",false)
           clearInterval(this.timer)
          
        }
    }, 1000);
  },
};
</script>

<style scoped lang="less">
   span{
    font-size: 0.46rem;
    color: #424242;
    background: #ffffff;
   }
</style>