<template>
  <div class="login">
    <div class="logo">
      <div class="logo-pic">
        <img :src="logo" alt="" />
      </div>
    </div>
    <div class="content">
      <div class="phone">
        <input
          type="tel"
          placeholder="请输入手机号"
          maxlength="11"
          v-model="phone"
          @input="changeBtn"
        />
        <span v-show="!isPassWordLogin" @click="getCode" v-if="!haveGotCode"
          >获取验证码</span
        >
        <Timer v-if="haveGotCode" ></Timer>
      </div>
      <div class="border" ref="phoneBorder"></div>
      <div class="password">
        <input
          :type="type"
          :placeholder="placeholder"
          :maxlength="maxlength"
          v-model="code"
          @input="changeBtn"
        />
      </div>
      <div class="border" ref="codeBorder"></div>
    </div>
    <div class="btn">
      <button :disabled="disabled" @click="login" ref="loginBtn">登录</button>
    </div>
    <h2 class="tip">{{ rigistTip }}</h2>

    <div class="agree">
      <div class="isagree">
        <input type="checkbox" name="" id="" v-model="checked" />
      </div>
      <div class="msg">
        我已阅读并同意 <a href="">《{{ user }}用户协议》</a
        ><a href="">《隐私政策》</a>，并授权{{ user }}使用该{{
          user
        }}账号信息（如昵称、头像、收货地址）进行统一管理
      </div>
    </div>
    <div class="changeWay">
      <button @click="changeLoginWay">{{ loginTip }}</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import { Toast } from "vant";
import Timer from "../Timer/Timer.vue";
import "js-base64";
import pubsub from "pubsub-js";
Vue.use(Toast);
export default {
  data() {
    return {
      disabled: true,
      checked: "",
      phone: "",
      code: "",
      isPassWordLogin: false,
      loginTip: "切换密码登录",
      rigistTip: "未验证的手机号验证后自动创建用户",
      type: "number",
      placeholder: "请输入验证码",
      maxlength: "",
      haveGotCode: false,
    };
  },
  methods: {
    changeLoginWay() {
      this.isPassWordLogin = !this.isPassWordLogin;
      if (this.isPassWordLogin) {
        this.type = "password";
        this.placeholder = "请输入密码";
        this.maxlength = 16;
      } else {
        this.type = "number";
        this.placeholder = "请输入验证码";
        this.maxlength = 4;
      }
    },
    changeBtn() {
      if (!this.isPassWordLogin) {
        if (
          this.phone.length == "11" &&
          this.code.length == 4 &&
          this.checked
        ) {
          this.disabled = false;
        } else {
          this.code = this.code.slice(0, 4);
        }
      } else {
        if (
          this.phone.length == "11" &&
          this.code.length >= 8 &&
          this.checked
        ) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      }
    },
    isValidatePhone() {
      var reg = /^1[3-9][0-9]{9}$/;
      if (reg.test(this.phone)) {
        return true;
      } else {
        return false;
      }
    },
    login() {
      if (this.isValidatePhone()) {
        var phone = this.phone;
        var code = this.code;
        if (!this.isPassWordLogin) {
          axios
            .post("http://47.97.22.215:3000/login/postCode", {
              phone,
              code,
            })
            .then((val) => {
              Toast.loading("加载中");
              if (val.data.successLogin) {
                Toast.success("登录成功");
                history.back();
              } else {
                Toast.fail("验证码错误");
              }
            })
            .catch((err) => {
              Toast.fail("验证码错误");
            });
        } else {
          axios
            .post("http://47.97.22.215:3000/loginbypassword", {
              phone,
              password: require("js-base64").Base64.encode(code),
            })
            .then((val) => {
              Toast.loading("加载中");
              if (val.data.successLogin) {
                Toast.success("登录成功");
                history.back();
              } else {
                Toast.fail("密码错误");
              }
            })
            .catch((err) => {
              Toast.fail("登录失败");
            });
        }
      } else {
        Toast("无效手机号");
      }
    },
    getCode() {
      if (this.isValidatePhone()) {
        var _this = this;
        axios
          .get("http://47.97.22.215:3000/login", {
            params: {
              phone: _this.phone,
            },
          })
          .then((res) => {
            if (!_this.haveGotCode) {
              if (res.data.haveSendCode) {
                Toast("验证码已发送");
                _this.haveGotCode = true;
              } else {
                 Toast("验证码获取失败")
              }
            }
          })
          .catch((err) => {
            Toast.fail("验证码获取失败");
            alert("a")
          });
      }
    },
  },
  watch: {
    checked(newval) {
      if (!newval) {
        this.disabled = true;
      } else {
        if (this.phone.length == 11 && this.code.length >= 4) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      }
    },
    isPassWordLogin(newVal) {
      if (newVal) {
        (this.loginTip = "切换验证码登录"), (this.rigistTip = "");
        document.title = "密码登录";
      } else {
        (this.loginTip = "切换密码登录"),
          (this.rigistTip = "未验证的手机号验证后自动创建用户");
        document.title = "验证码登录";
      }
    },
    disabled(newval) {
      if (!newval) {
        this.$refs.loginBtn.style.background = "#F8D658";
        this.$refs.loginBtn.style.color = "black";
      } else {
        this.$refs.loginBtn.style.background = "#FFF9E1";
        this.$refs.loginBtn.style.color = "#e4e4e4";
      }
    },
  },
  props: {
    logo: {
      type: String,
      // default:
      //   "https://www.luminaryleadership.net/news/wp-content/uploads/2012/07/Logo-black.jpg",
      default:
        "http://eb118-file.cdn.bcebos.com/upload/51C26083C8431427BA339DE3844FE576?x-bce-process=style/wm_ai",
    },
    user: {
      type: String,
      default: "App",
    },
  },
  components: {
    Timer,
  },
  mounted() {
    document.title = "验证码登录";
    var _this=this
    pubsub.subscribe("timeout", function () {
       _this.haveGotCode=false
    });
  },
};
</script>

<style lang="less" scoped>
.login {
  display: flex;
  flex-direction: column;
  .logo {
    height: 0.23 * 18.5rem;
    background: #ffffff;
    position: relative;
    .logo-pic {
      position: absolute;
      width: 0.11 * 18.5rem;
      height: 0.11 * 18.5rem;
      top: 50%;
      left: 50%;
      margin-top: -0.055 * 18.5rem;
      margin-left: -0.055 * 18.5rem;
      border-radius: 5px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
  } //logo部分
  .content {
    height: 0.16 * 18.5rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    background: #ffffff;
    .phone {
      height: 0.075 * 18.5rem;
      font-size: 0.46rem;
      input {
        height: 100%;
        color: #424242;
        &::placeholder {
          color: #e4e4e4;
        }
      }
    }
    .password {
      height: 0.075 * 18.5rem;
      font-size: 0.46rem;

      input {
        height: 100%;
        color: #424242;
        &::placeholder {
          color: #e4e4e4;
        }
      }
    }

    .border {
      height: 0.5px;

      background: #e4e4e4;
    }
  } //输入框部分
  .btn {
    height: 0.12 * 18.5rem;
    background: #ffffff;
    padding-left: 0.8rem;
    padding-right: 0.8rem;

    button {
      height: 0.08 * 18.5rem;
      width: 100%;
      border: none;
      border-radius: 5px;
      background: #fff9e1;
      margin-top: 0.04 * 18.5rem;
      font-size: 0.46rem;
      color: #e4e4e4;
    }
  }
  .tip {
    height: 0.04 * 18.5rem;
    background: #ffffff;
    text-align: center;
    color: #898989;
    font-size: 0.3rem;
    font-weight: normal;
    line-height: 0.04 * 18.5rem;
  }
  .agree {
    height: 0.1 * 18.5rem;
    background: #ffffff;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    display: flex;
    padding-top: 0.8rem;
    .isagree {
      flex: 1;
      width: 0.46rem;
      height: 0.46rem;

      text-align: center;
      input {
      }
    }
    .msg {
      flex: 9;
    }
  }
  .changeWay {
    height: 0.35 * 18.5rem;
    background: #ffffff;
    text-align: center;

    button {
      font-size: 0.46rem;
      background: none;
      border: none;
      margin-top: 0.25 * 18.5rem * 0.3;
    }
  }
}
</style>